body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f4f4;
}

.items-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 20px;
  padding: 20px;
}

.item-card {
  flex: 0 1 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  background-color: #fff;
  padding: 16px;
  margin-bottom: 20px;
}

.item-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.item-info {
  margin: 4px 0 0;
  font-size: 0.9rem;
  color: #666;
}
.item-info h3 {
  margin: 0;
  padding: 0;
  font-size: 1.1rem;
  color: #333;
}
.item-info p {
  margin: 4px 0 0;
  font-size: 0.9rem;
  color: #666;
}
.items-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  gap: 20px;
}
button {
  cursor: pointer;
  padding: 8px 12px;
  border: none;
  border-radius: 5px;
  font-size: 0.9rem;
  font-weight: 600;
  color: white;
  outline: none;
  transition: background-color 0.3s;
  background-color: #28a745;
}
.button-container {
  display: flex;
  gap: 10px;
  padding-top: 10px;
}

button:hover {
  transform: scale(1.05);
}

.message {
  background-color: #8aa991;
  color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  width: 50%;
  margin: 20px auto;
  text-align: center;
  font-weight: bold;
}

.empty-message {
  background-color: #ffc107;
  color: #212529;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  width: 50%;
  margin: 20px auto;
  text-align: center;
  font-weight: bold;
}

.inventory-form-container {
  padding: 20px;
}

.inventory-form {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  margin: auto;
}

.inventory-form .form-group label {
  font-weight: bold;
  margin-bottom: 8px;
}

.inventory-form input {
  border: 1px solid #ccc;
  margin-bottom: 20px;
}

.inventory-form .submit-btn {
  background-color: #28a745;
}
.update-btn {
  background-color: #4CAF50;
}

.delete-btn {
  background-color: #f44336;
}

.update-btn:hover, .delete-btn:hover {
  opacity: 0.85;
}

.edit-input {
  width: 90%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.save-btn, .cancel-btn {
  cursor: pointer;
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  margin-right: 5px;
  transition: opacity 0.3s ease;
}

.save-btn {
  background-color: #4CAF50;
  color: white;
}

.cancel-btn {
  background-color: #f44336; 
  color: white;
}

.save-btn:hover, .cancel-btn:hover {
  opacity: 0.8; 
}

.form-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.save-btn, .cancel-btn {
  margin-top: 10px;
}
